.container {
  position: relative;
  width: 100%;
  height: 85%;
  margin: 0 !important;
}

.project-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 50% 50%;
  opacity: 1;
  display: block;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  /* top: 50%; */
  left: 50%;
  transform: translate(-50%, -100%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
  height: 100%;
}

.container:hover .image {
  opacity: 0.3;
}

.container:hover .middle {
  opacity: 0.9;
}

.text {
  background-color: #686868;
